import React, { useState } from "react";
import axios from "axios";
import swal from 'sweetalert';


const Contact = () => {

    const [forms, setForms] = useState([{
        name: "",
        email: "",
        message: ""

    }]);

    const sendMAil = () => {
        let reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (reg.test(forms.email) === false) {
            swal({
                title: "Warning",
                text: "Please provide you email!",
                icon: "error",
                button: "Close!",
            });
        }
        else {

            if (forms.name == null || !forms.name.replace(/\s/g, '').length || forms.email == null || !forms.email.replace(/\s/g, '').length || forms.message == null || !forms.message.replace(/\s/g, '').length) {
                swal({
                    title: "Good job!",
                    text: "You clicked the button!",
                    icon: "error",
                    button: "Aww yiss!",
                });
            }

            let formData = new FormData();
            formData.append("name", forms.name);
            formData.append("email", forms.email);
            formData.append("message", forms.message);

            const url = "https://dilavermusa.com/backend/functions.php";
            axios.post(url, formData).then(vlerat => {

                document.getElementById("name").value = "";
                document.getElementById("email").value = "";
                document.getElementById("message").value = "";

            });
        }

    }

    const typeping = (vlerat, vlerat1) => {
        if (vlerat1 === 1) {
            setForms({
                ...forms,
                name: vlerat.target.value
            });
        } else if (vlerat1 === 2) {
            setForms({
                ...forms,
                email: vlerat.target.value
            });
        } else {

            setForms({
                ...forms,
                message: vlerat.target.value
            });
        }
    }

    return (

        <div className="content-wrap">
            <div className="mccan page">
                <h3 className="title-small">LOCATION</h3>
                <h2 className="title">Contact Me</h2>
                <div className="content">
                    <div className="row">
                        <div className="col-md-12">
                            <p>If you are interested in any work you are free to contact me at any time.</p>
                            <div className="list">
                                <ul>
                                    <li><strong>Phone :</strong>  +355 69 23 59 162</li>
                                    <li className="text-warning" ><strong>Email :</strong> rei.etrc@gmail.com</li>
                                    <li><strong>Address :</strong> Aleksander Goga, Street 15, Durres, Albania.</li>
                                    <li><strong>Map :</strong> <a className="text-warning" href="https://goo.gl/maps/G2Jf37YKtWq5dBEaA">via Google Maps</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div className="divider1"></div>
                    <div className="row">
                        <div className="col-md-8">
                            <h6 className="mb-20 col-12 text-center"><strong>Get in touch</strong></h6>
                            <form>
                                <div className="message d-none">
                                    <div className="alert notice"></div>
                                </div>
                                <label for="name" className="screen-reader-text">Name</label>
                                <input type="text" name="name" id="name" placeholder="Name *" required="" onChange={(e) => typeping(e, 1)} />
                                <label for="email" className="screen-reader-text">Email</label>
                                <input type="email" name="email" id="email" placeholder="Email *" required="" onChange={(e) => typeping(e, 2)} />
                                <label for="message" className="screen-reader-text">Message</label>
                                <textarea onChange={(e) => typeping(e, 3)} name="message" id="message" cols="30" rows="5" placeholder="Message *" required=""></textarea>
                                <div className="col-12 d-flex justify-content-center">
                                    <input onClick={sendMAil} type="button" className="btn ajax" value="Send E-mail" />

                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default Contact;