import React, { useEffect } from "react";
import '../App.css';
import '../assets/css/bootstrap.min.css';
import '../assets/css/et-lineicons.css';
import '../assets/css/themify-icons.css';
import '../assets/css/style.css';
import '../assets/css/style-animations.css';
import '../assets/css/owlcarousel.css';
import Display from "./display/display";
import Contact from './contact/contact';
import Resume from "./resume/resume";
import Portofolio from "./portofolio/portofolio";
import { Route, Switch } from "react-router-dom";
import Info from "./info/info";
import Home from "./home/home";
import Topmenu from "./topmenu/topmenu";
import Logo from "./logo/logo";
import Sidemenu from "./sidemenu/sidemenu";
import Hiddenmenu from "./hiddenmenu/hiddenmenu";
import axios from "axios";

const Website = (vlerat) => {

    useEffect(() => {
        fetch('https://extreme-ip-lookup.com/json/')
            .then(res => res.json())
            .then(response => {


                let formData = new FormData();
                formData.append("track_ip", response.query);
                formData.append("track_city", response.city);
                formData.append("track_country", response.country);
                formData.append("track_continent", response.continent);
                formData.append("track_net", response.isp);
                formData.append("track_position", response.lat + " " + response.lon);

                const url = 'https://dilavermusa.com/backend/insert.php';
                axios.post(url, formData).then(vlerat => {

                });
            })

    }, []);
    //    expanded-hero expanded-hero-completed
    return (
        <div class="cv has-additional-menu-content websitebody " style={{
            height: '100vh'
        }}>
            <Logo />
            <Topmenu changeflag={vlerat.flagchange} />
            <Sidemenu />

            <Switch>
                <Route exact path="/resume" component={Resume} ></Route>
                <Route exact path="/portofolio" component={Portofolio} ></Route>
                <Route exact path="/display" component={Display} ></Route>
                <Route exact path="/contact" component={Contact} ></Route>
                <Route exact path="*" component={Home} ></Route>

            </Switch>
            <Hiddenmenu />
            <Info />
        </div>
    );
}

export default Website;