import React, { useState } from "react";
import { Link } from "react-router-dom";

const Sidemenu = () => {

    const [classval, setClass] = useState(0);

    const openModal = () => {
        if (classval == 0) {
            document.body.classList.add('mobile-menu-opened');
            setClass(1);
        } else {
            document.body.classList.remove('mobile-menu-opened');
            setClass(0);
        }
    }

    return (
        <div>
            <div className="menu-overlay"></div>
            <div className="site-menu-toggle">
                <Link onClick={openModal} className="ti"> <span className="screen-reader-text">Toggle navigation</span> </Link>
            </div>
            <div id="social-profiles">
                <nav className="social-menu">
                    <ul>
                        <li className="menu-item"><a href="https://www.facebook.com/erimusa11/" target="_blank"><span className="ti"></span></a></li>
                        <li className="menu-item"><a href="https://www.linkedin.com/in/dilaver-eri-musa-681332181/" target="_blank"><span className="ti"></span></a></li>
                        <li className="menu-item"><a href="https://www.instagram.com/erimusa11" target="_blank"><span className="ti"></span></a></li>
                    </ul>
                </nav>
            </div>
            <div class="hero-media">
                <div class="owl-carousel owl-loaded" data-expand-duration="800">

                    <div class="owl-stage-outer">
                        <div class="owl-stage  wierd " >
                            <div class="owl-item active  " style={{ width: "100vh" }}>
                                <div class="item   baground  " style={{ height: "100vh", width: "100vh" }}  >
                                </div>
                            </div>
                        </div>
                    </div>

                    <div class="owl-expand"><a href="https://api.whatsapp.com/send?phone=355692359162"> <i class="fab fa-whatsapp " style={{ fontSize: 45 }}></i>
                    </a>

                    </div>

                    <div class="owl-dots disabled">
                    </div>
                </div>
                <span class="overlay">
                </span>
                <span class="ti ti-spin ti-loading finished">
                </span> </div>
        </div >
    );
}

export default Sidemenu;