import React, { useState } from "react";
import "../../App.css";
import LocaleContext from "../../components/globalCon";
import { Link } from "react-router-dom";

const Leftmenu = () => {
    //toggled
    const [dropmenu, setDropmenu] = useState(0);
    const dropdownmenu = () => {

        if (dropmenu == 0) {
            setDropmenu(1);
        } else {
            setDropmenu(0);
        }

    }
    return (
        <LocaleContext.Consumer>
            {(locale) => (
                <div className="main-sidebar sidebar-style-2 d-none d-md-block">
                    <aside id="sidebar-wrapper">
                        <div className="sidebar-brand">
                            <a href="#">   <span
                                className="logo-name"> {locale.name} {locale.surname}</span>
                            </a>
                        </div>
                        <ul className="sidebar-menu">
                            <li className="menu-header">Main</li>
                            <li className="dropdown">
                                <Link to="/dashboard" className="nav-link"><i data-feather="monitor"></i><span>Dashboard</span></Link>
                            </li>
                            <li className="dropdown">
                                <Link to="/dashboard/visitors" className="nav-link"><i data-feather="monitor"></i><span>Visitor tracker</span></Link>
                            </li>
                            <li className="dropdown">
                                <a href="#" onClick={dropdownmenu} className={dropmenu === 0 ? "menu-toggle nav-link has-dropdown" : "menu-toggle nav-link has-dropdown toggled"}><i
                                    data-feather="briefcase"></i><span>Menu free</span></a>
                                <ul className="dropdown-menu  " style={{ display: dropmenu ? 'block' : 'none' }}  >
                                    <li><a className="nav-link" href="#">Menu free 1</a></li>
                                    <li><a className="nav-link" href="#">DMenu free 2</a></li>
                                </ul>
                            </li>
                        </ul>
                    </aside>
                </div >
            )}
        </LocaleContext.Consumer>
    );
}

export default Leftmenu;