import React, { useState } from "react";
import "../assetsDashboard/css/style.css";
import "../assetsDashboard/css/components.css";
import "../assetsDashboard/css/custom.css";
import Topmenu from "./topmenu/topmenu";
import Leftmenu from "./leftmenu/leftmenu";
import Addportofolio from "./body/addportofolio/addportofolio";
import Footer from "./footer/footer";
import { Route, Redirect, Switch } from "react-router-dom";
import LocaleContext from "../components/globalCon";
import Profile from "./body/profile/profile";
import Track from "./body/track/track";
const Dashboard = (vlerat) => {

    const [themeColor, setThemeColor] = useState(localStorage.getItem("theme") == null ? " light theme-white " : localStorage.getItem("theme"));

    const [themeSidebar, setThemeSidebar] = useState(localStorage.getItem("sidevar") == null ? " light-sidebar " : localStorage.getItem("sidevar"));

    const [topColor, setTopCor] = useState(localStorage.getItem("topcol") == null ? " theme-white " : localStorage.getItem("topcol"));

    const [tick, setTick] = useState(0);

    const nightmode = (vlera) => {
        if (vlera == 1) {
            setThemeColor(" light ");
            localStorage.setItem("theme", " light ");
        } else if (vlera == 2) {
            setThemeColor(" dark ");
            localStorage.setItem("theme", " dark ");
        } else if (vlera == 3) {

            setThemeSidebar(" light-sidebar ");
            localStorage.setItem("sidevar", " light-sidebar ");
        } else if (vlera == 4) {
            setThemeSidebar(" dark-sidebar");
            localStorage.setItem("sidevar", " dark-sidebar ");
        } else if (vlera == 5) {
            setTopCor(" theme-white ");
            localStorage.setItem("topcol", " theme-white ");
            setTick(5);
        } else if (vlera == 6) {
            setTopCor(" theme-cyan ");
            localStorage.setItem("topcol", " theme-cyan ");
            setTick(6);
        } else if (vlera == 7) {
            setTopCor(" theme-black ");
            localStorage.setItem("topcol", " theme-black ");
            setTick(7);

        } else if (vlera == 8) {
            setTopCor(" theme-purple ");
            localStorage.setItem("topcol", " theme-purple ");
            setTick(8);

        } else if (vlera == 9) {
            setTopCor(" theme-orange ");
            localStorage.setItem("topcol", " theme-orange ");
            setTick(9);

        } else if (vlera == 10) {
            setTopCor(" theme-green ");
            localStorage.setItem("topcol", " theme-green ");
            setTick(10);

        } else if (vlera == 11) {
            setTopCor(" theme-red ");
            localStorage.setItem("topcol", " theme-red ");
            setTick(11);

        }
    }
    return (

        <LocaleContext.Consumer >
            {(locale) => (
                locale.userid != null ?
                    <div className={themeColor + themeSidebar + topColor + " sidebar-gone"} style={{
                        height: '100vh'
                    }}>
                        <div id="app">
                            <div className="main-wrapper main-wrapper-1">
                                <div className="navbar-bg"></div>
                                <Topmenu logoutperson={vlerat.logout} />
                                <Leftmenu />
                                <Switch>

                                    <Route exact path="/dashboard" component={Addportofolio} ></Route>
                                    <Route render={(props) => (<Profile personId={locale.userid} tickVal={tick}  {...props} nighton={nightmode} />)} exact path="/dashboard/profile" ></Route>
                                    <Route exact path="/dashboard/visitors" component={Track} ></Route>


                                </Switch>
                                <Footer />
                            </div>
                        </div>
                    </div> : <Redirect push to="/login" />
            )}
        </LocaleContext.Consumer >
    );
}

export default Dashboard;