import React, { useState } from "react";
import { Link } from "react-router-dom";
import LocaleContext from "../../components/globalCon";
const Topmenu = (vlerat) => {
    const [toggle, setToggle] = useState(0);
    const togglelogmenu = () => {

        if (toggle === 0) {
            setToggle(1);
        } else {
            setToggle(0);

        }
    }
    return (
        <LocaleContext.Consumer>
            {(locale) => (
                <nav className="navbar navbar-expand-lg main-navbar sticky">
                    <div className="form-inline mr-auto">
                        <ul className="navbar-nav mr-3">
                            <li><a href="#" data-toggle="sidebar" className="nav-link nav-link-lg
                    collapse-btn"> <i data-feather="align-justify"></i></a></li>
                            <li><a href="#" className="nav-link nav-link-lg fullscreen-btn">
                                <i data-feather="maximize"></i>
                            </a></li>
                            <li>
                                <form className="form-inline mr-auto">
                                    <div className="search-element">
                                        <input className="form-control" type="search" placeholder="Search" aria-label="Search" data-width="200" />
                                        <button className="btn" type="button">
                                            <i className="text-dark fas fa-search"></i>
                                        </button>
                                    </div>
                                </form>
                            </li>
                        </ul>
                    </div>
                    <ul className="navbar-nav navbar-right">
                        <li className={toggle == 0 ? 'dropdown' : 'dropdown show'}><button type="button" onClick={togglelogmenu} data-toggle="dropdown"
                            className="btn btn-sm btn-light nav-link dropdown-toggle nav-link-lg nav-link-user">  <i className=" text-dark fab fa-first-order"></i><span className="d-sm-none d-lg-inline-block"></span></button>
                            <div className={toggle == 0 ? 'dropdown-menu dropdown-menu-right pullDown ' : 'dropdown-menu dropdown-menu-right pullDown show'}>
                                <div className="dropdown-title">Hello {locale.name} {locale.surname} </div>
                                <Link onClick={togglelogmenu} to="/dashboard/profile" className="dropdown-item has-icon"> <i className="far fa-user"></i> Profile</Link>
                                <Link onClick={togglelogmenu} to="/dashboard/visitors" className="dropdown-item has-icon"> <i className="far fa-user"></i> Visitor Track</Link>


                                <Link onClick={togglelogmenu} to="/dashboard/" className="dropdown-item has-icon"> <i className="far fa-user"></i> Dashboard</Link>

                                <Link to="/" className="dropdown-item has-icon"> <i className="fas fa-blog"></i> Return to the website </Link>




                                <div className="dropdown-divider"></div>
                                <Link onClick={vlerat.logoutperson} className="dropdown-item has-icon text-danger"> <i className="fas fa-sign-out-alt"></i>
Logout
</Link>
                            </div>
                        </li>
                    </ul>
                </nav>
            )}
        </LocaleContext.Consumer>
    );
}

export default Topmenu;