import React, { Component } from "react";
import DataTable from 'react-data-table-component';
import axios from "axios";

class Track extends Component {

    state = {
        columns: [
            {
                name: 'IP Visitor',
                selector: 'track_ip',
                sortable: true,
                width: "30vh"

            },
            {
                name: 'Tot. Time Visited',
                selector: 'TotalTime',
                sortable: true,


            },
            {
                name: 'Last Visit',
                selector: 'EnterLAst',
                sortable: true,

            },
            {
                name: 'City / Country / Continent',
                selector: 'track_city',
                sortable: true,

            },

            {
                name: 'Net Provider',
                selector: 'track_net',
                sortable: true,

            },

        ],

        data: []

    }

    componentDidMount() {
        axios.get("https://dilavermusa.com/backend/showTrack.php").then(vlerat => {
            this.setState({ data: vlerat.data });
        })
    }

    render() {
        return (

            <div className="main-content col-12">
                <section className="section">
                    <div className="section-body">
                        <div class="card">
                            <div class="card-body">
                                <DataTable
                                    title="Your visitors"
                                    columns={this.state.columns}
                                    data={this.state.data}
                                    pagination={true}
                                    allowOverflow={true}
                                    pointerOnHover={true}
                                    overflowY={true}
                                    width="auto"
                                    fixedHeader={true}
                                />
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}
export default Track;