import React, { Component } from "react";
import axios from 'axios';
import { Link } from "react-router-dom";

export default class display extends Component {

    state = {
        contents: [],
        init: 0
    }

    componentDidMount() {

        let formData = new FormData();
        formData.append("id_post", this.props.location.state.post_id);

        const url = "https://dilavermusa.com/backend/showContents.php";
        axios.post(url, formData).then(vlerat => {

            this.setState({ contents: vlerat.data, init: 1 })

        });
    }

    renderProduct = () => {
        return <div className="content-wrap">
            <div className="mccan page">
                <div className="col-12 d-flex justify-content-center">
                    <Link to="/portofolio" className="btn">Go Back</Link>
                </div>
                <h3 className="title-small">Project</h3>
                <h2 className="title">{this.state.contents[0].project_name}</h2>
                <div className="content">
                    <div className="grid-container">
                        <div className="grid-column">
                            <div className="project-listing">
                                <div className="project">
                                    <div className="media">
                                        <a href="#"> <img src={process.env.PUBLIC_URL + "/projectimg/" + this.state.contents[0].img_two} alt="" /> </a>
                                    </div>
                                    <header className="header">
                                        <div className="excerpt">
                                            <p>{this.state.contents[0].project_desc}</p>

                                            <div className="list">
                                                <ul>
                                                    <li><strong>Project Category :</strong> {this.state.contents[0].project_category}</li>
                                                    <li><strong>URL Website : </strong><a href={this.state.contents[0].project_link} target="_blank" class="text-warning">{this.state.contents[0].project_link}</a></li>
                                                    <li><strong>Tools :</strong> {this.state.contents[0].project_tools}</li>

                                                </ul>
                                            </div>
                                        </div>
                                    </header>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
    render() {
        return (
            this.state.init ? <div>{this.renderProduct()}</div> : <div>loading...</div>
        );
    }
}