import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import LocaleContext from "../src/components/globalCon";
import Website from "./components/website";
import Dashboard from "./dashboard/dashboard";
import Login from "./dashboard/login/login";
import axios from "axios";
import swal from 'sweetalert';

document.addEventListener('contextmenu', function (e) {
  e.preventDefault();
});

class App extends Component {

  state = {
    userid: null,
    username: "",
    password: "",
    name: "",
    surname: "",
    alert: true,
    defaultlang: "US"
  }

  userlogout = () => {
    this.setState({
      userid: null,
      username: "",
      password: "",
      name: "",
      surname: "",
      alert: true
    });
  }
  loginbtnnow = () => {

    if (this.state.username != "" && this.state.password != "") {

      let submitform = "loginuser";
      let formData = new FormData();
      formData.append("loginuser", submitform);
      formData.append("username", this.state.username);
      formData.append("password", this.state.password);


      const url = "https://dilavermusa.com/backend/insert.php";
      axios.post(url, formData).then(vlerat => {

        if (vlerat.data === "dont exist") {
          this.setState({
            alert: false,
          });
          document.getElementById("email").value = "";
          document.getElementById("password").value = "";

        } else {
          this.setState({
            userid: vlerat.data[0].person_id,
            name: vlerat.data[0].person_name,
            surname: vlerat.data[0].person_subname,
          });
        }
      });

    } else {
      swal({
        title: "Warning",
        text: "To enter you need provide your correct credential first!",
        icon: "error",
        button: "Close!",
      });
    }

  }

  getinputval = (vlera1, vlera2) => {

    if (vlera1 == 1) {
      this.setState({ username: vlera2 });

    } else if (vlera1 == 2) {
      this.setState({ password: vlera2 });
    }
  }


  updateflag = (vlerat) => {

    console.log(vlerat);
    this.setState({ defaultlang: vlerat });


  }

  render() {
    return (
      <LocaleContext.Provider value={this.state}>
        <BrowserRouter>
          <Switch>

            <Route path="/dashboard" render={(props) => (
              <Dashboard {...props} logout={this.userlogout} />)} ></Route>

            <Route exact path="/login" render={(props) => (
              <Login {...props} formdata={this.getinputval} kalimi={this.loginbtnnow} />
            )} />

            <Route path="*" render={(props) => (
              <Website {...props} flagchange={this.updateflag} />
            )} />


          </Switch>

        </BrowserRouter >
      </LocaleContext.Provider>
    );
  }
}

export default App;
