import React from "react";
import Logo from "../../assets/images/logo.png";
import { Link } from "react-router-dom";

export default function logo() {

    return (
        <header id="identity">
            <div className="logo  ">
                <Link to="/login" className="logo-link"  > <img src={Logo} className="logo" /> </Link>
            </div>
        </header>
    );
}