import React, { useState } from "react";

const Colormenu = (vlerat) => {

    const [setting, setSettig] = useState(0);

    const chnagesetting = () => {
        if (setting == 0) {
            setSettig(1);
        } else {
            setSettig(0);
        }
    }

    return (
        <div className={setting == 1 ? "settingSidebar  showSettingPanel" : "settingSidebar"}>
            <a href="javascript:void(0)" onClick={chnagesetting} className="settingPanelToggle"> <i className="fa fa-spin fa-cog"></i>
            </a>
            <div className="settingSidebar-body ps-container ps-theme-default">
                <div className=" fade show active">
                    <div className="setting-panel-header">Setting Panel
</div>
                    <div className="p-15 border-bottom">
                        <h6 className="font-medium m-b-10">Select Layout</h6>
                        <div className="selectgroup layout-color w-50">
                            <label className="selectgroup-item">
                                <input type="radio" onClick={() => vlerat.nightmode(1)} name="value" value="1" className="selectgroup-input-radio select-layout" />
                                <span className="selectgroup-button">Light</span>
                            </label>
                            <label className="selectgroup-item">
                                <input onClick={() => vlerat.nightmode(2)} type="radio" name="value" value="2" className="selectgroup-input-radio select-layout" />
                                <span className="selectgroup-button">Dark</span>
                            </label>
                        </div>
                    </div>
                    <div className="p-15 border-bottom">
                        <h6 className="font-medium m-b-10">Sidebar Color</h6>
                        <div className="selectgroup selectgroup-pills sidebar-color">
                            <label className="selectgroup-item">
                                <input type="radio" name="icon-input" value="1" className="selectgroup-input select-sidebar" onClick={() => vlerat.nightmode(3)} />
                                <span className="selectgroup-button selectgroup-button-icon" data-toggle="tooltip"
                                    data-original-title="Light Sidebar"><i className="fas fa-sun"></i></span>
                            </label>
                            <label className="selectgroup-item">
                                <input onClick={() => vlerat.nightmode(4)} type="radio" name="icon-input" value="2" className="selectgroup-input select-sidebar" />
                                <span className="selectgroup-button selectgroup-button-icon" data-toggle="tooltip"
                                    data-original-title="Dark Sidebar"><i className="fas fa-moon"></i></span>
                            </label>
                        </div>
                    </div>
                    <div className="p-15 border-bottom">
                        <h6 className="font-medium m-b-10">Color Theme</h6>
                        <div className="theme-setting-options">
                            <ul className="choose-theme list-unstyled mb-0">
                                <li onClick={() => vlerat.nightmode(5)} title="white" className={vlerat.valTick == 5 ? "active" : ""}>
                                    <div className="white"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(6)} className={vlerat.valTick == 6 ? "active" : ""} title="cyan">
                                    <div className="cyan"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(7)} className={vlerat.valTick == 7 ? "active" : ""} title="black">
                                    <div className="black"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(8)} className={vlerat.valTick == 8 ? "active" : ""} title="purple">
                                    <div className="purple"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(9)} className={vlerat.valTick == 9 ? "active" : ""} title="orange">
                                    <div className="orange"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(10)} className={vlerat.valTick == 10 ? "active" : ""} title="green">
                                    <div className="green"></div>
                                </li>
                                <li onClick={() => vlerat.nightmode(11)} className={vlerat.valTick == 11 ? "active" : ""} title="red">
                                    <div className="red"></div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Colormenu;