import React, { Component } from "react";
import axios from "axios";
import swal from 'sweetalert';
import LocaleContext from "../../../components/globalCon";

class Addportofolio extends Component {

    state = {
        projectname: "",
        projecttools: "",
        projectcategory: "",
        projecturl: "",
        imageone: "",
        imagetwo: "",
        projectdesc: ""
    }

    updateDateForm = (vlerat1, vlerat2) => {

        if (vlerat2 == 1) {
            this.setState({ projectname: vlerat1.target.value });
        } else if (vlerat2 == 2) {
            this.setState({ projecttools: vlerat1.target.value });
        } else if (vlerat2 == 3) {
            this.setState({ projectcategory: vlerat1.target.value });
        } else if (vlerat2 == 4) {
            this.setState({ projecturl: vlerat1.target.value });
        } else if (vlerat2 == 5) {
            this.setState({ projectdesc: vlerat1.target.value });
        } else if (vlerat2 == 6) {
            this.setState({ imageone: vlerat1.target.files[0] });
        } else if (vlerat2 == 7) {
            this.setState({ imagetwo: vlerat1.target.files[0] });
        }

    }

    sendformdatasback = (vlera) => {

        if (this.state.projectname != "" && this.state.projecttools != "" && this.state.projectdesc != "" && this.state.projecturl != "" && this.state.projectdesc != "" && this.state.imageone != "" && this.state.imagetwo != "" && vlera != null) {

            let formData = new FormData();
            formData.append("project_name", this.state.projectname);
            formData.append("project_tools", this.state.projecttools);
            formData.append("project_desc", this.state.projectdesc);
            formData.append("project_link", this.state.projecturl);
            formData.append("user_id", 1);
            formData.append("project_category", this.state.projectdesc);
            formData.append("img_one", this.state.imageone, this.state.imageone.name);
            formData.append("img_two", this.state.imagetwo, this.state.imagetwo.name);

            const url = "https://dilavermusa.com/backend/insert.php";
            axios.post(url, formData).then(vlerat => {

                document.getElementById("projectname").value = "";
                document.getElementById("projecttools").value = "";
                document.getElementById("projectcatego").value = "";
                document.getElementById("projecturl").value = "";
                document.getElementById("textdesc").value = "";
                document.getElementById("image1").value = "";
                document.getElementById("image2").value = "";
                if (vlerat.data === "success") {
                    swal({
                        title: "Congratulation!",
                        text: "You have created a new post!",
                        icon: "success",
                        button: "Aww yiss!",
                    });
                }
            });

        } else {

            swal({
                title: "Alert!",
                text: "Complate all the fields to create a new post!",
                icon: "error",
                button: "Aww yiss!",
            });
        }
    }

    render() {
        return (

            <LocaleContext.Consumer >

                {(locale) => (
                    <div className="main-content">
                        <section className="section">
                            <div className="section-body">
                                <div className="card">
                                    <div className="card-header">
                                        <h4>Create a new post for your website</h4>
                                    </div>
                                    <div className="card-body row">
                                        <div className="form-group col-md-6 col-sm-12">
                                            <div className="input-group mb-2">
                                                <div className="input-group-prepend">
                                                    <div className="input-group-text">Project name:</div>
                                                </div>
                                                <input type="text" onChange={(e) => { this.updateDateForm(e, 1) }} className="form-control bg-light" id="projectname" placeholder="......" />
                                            </div>
                                        </div>
                                        <div className="form-group col-md-6 col-sm-12">
                                            <div className="input-group mb-2">
                                                <div className="input-group-prepend">
                                                    <div className="input-group-text">Project tools:</div>
                                                </div>
                                                <input type="text" onChange={(e) => { this.updateDateForm(e, 2) }} className="form-control bg-light" id="projecttools" placeholder="......" />
                                            </div>
                                        </div>
                                        <div className="form-group col-md-6 col-sm-12">
                                            <div className="input-group mb-2">
                                                <div className="input-group-prepend">
                                                    <div className="input-group-text">Project category:</div>
                                                </div>
                                                <input onChange={(e) => { this.updateDateForm(e, 3) }} type="text" className="form-control bg-light" id="projectcatego" placeholder="......" />
                                            </div>
                                        </div>
                                        <div className="form-group col-md-6 col-sm-12">
                                            <div className="input-group mb-2">
                                                <input onChange={(e) => { this.updateDateForm(e, 4) }} type="text" className="form-control text-right bg-light" id="projecturl" placeholder="......." />
                                                <div className="input-group-append">
                                                    <div className="input-group-text">Project Url</div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="d-flex justify-content-around col-12">
                                            <div class="custom-file col-5">
                                                <input onChange={(e) => { this.updateDateForm(e, 6) }} type="file" class="custom-file-input" id="image1" />
                                                <label class="custom-file-label" for="customFile">Multirow image...</label>
                                            </div>
                                            <div class="custom-file col-5">
                                                <input onChange={(e) => { this.updateDateForm(e, 7) }} type="file" class="custom-file-input" id="image2" />
                                                <label class="custom-file-label" for="customFile">Display image...</label>
                                            </div>
                                        </div>
                                        <div className="col-12 mt-3">
                                            <label>Project desciption</label>
                                            <textarea id="textdesc" onChange={(e) => { this.updateDateForm(e, 5) }} className="form-control"></textarea>
                                        </div>
                                        <div className="col-12 d-flex justify-content-center">
                                            <button onClick={() => this.sendformdatasback(locale.userid)} type="button" class="btn btn-md btn-icon icon-left btn-primary col-sm-12 col-md-2"><i class="far fa-file"></i> Create post</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </div>
                )}
            </LocaleContext.Consumer>
        );
    }
}

export default Addportofolio;