import React, { Component } from "react";
import ColorMenu from "../colormenu/colormenu";
import Backend from "../../../components/backend/insert.php";
import axios from "axios";
class Profile extends Component {
    state = {
        informacions: {},
        init: 0
    }
    componentDidMount() {

        let formData = new FormData();
        formData.append("personId", this.props.personId);
        const url = "https://dilavermusa.com/backend/insert.php";
        axios.post(url, formData).then(vlerat => {
            this.setState({ informacions: vlerat.data, init: 1 })
        });
    }

    waittherender = () => {
        return <div className="main-content">
            <section className="section">
                <div className="section-body">
                    <div className="card">
                        <div className="card-header">
                            <h4 className="text-center">My Prifile</h4>
                        </div>
                        <div className="card-body">
                            <form className="form-row">
                                <div className="form-group col-md-6">
                                    <label for="inputEmail4">Name</label>
                                    <input readonly type="text" className="form-control bg-light" id="inputEmail4" placeholder="Email" value={this.state.informacions[0].person_name} />
                                </div>
                                <div className="form-group col-md-6">
                                    <label for="inputPassword4">Surname </label>
                                    <input readonly value={this.state.informacions[0].person_subname} type="text" className="form-control  bg-light" id="inputPassword4" placeholder="Surname" />
                                </div>
                                <div className="form-group col-6">
                                    <label for="inputAddress">Username</label>
                                    <input readonly value={this.state.informacions[0].person_username} type="text" className="form-control  bg-light" id="inputAddress" placeholder="Username" />
                                </div>
                                <div className="form-group col-6" >
                                    <label for="inputAddress2">Password</label>
                                    <input readonly value={this.state.informacions[0].person_password} type="Password" className="form-control bg-light" id="inputAddress2" placeholder="********" />
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
            <ColorMenu valTick={this.props.tickVal} nightmode={this.props.nighton}></ColorMenu>
        </div >
    }

    render() {
        return (
            this.state.init ? <div>{this.waittherender()}</div> : <div>loading...</div>
        );
    }
}

export default Profile;