import React, { useState } from "react";
import { Redirect } from "react-router-dom";
const Post = (vlerat) => {

    const [vlera, setVlera] = useState(0);
    const [idPost, setIdPost] = useState(0);
    const ndryshimi = (vlera) => {
        setVlera(1);

        setIdPost(vlera);
    }

    return (<div className="col-md-6">
        <div className="desc">
            <div onClick={() => ndryshimi(vlerat.backdatas.id_post)} className="project"> <img src={process.env.PUBLIC_URL + "/projectimg/" + vlerat.backdatas.img_one} className="img-fluid" alt="" />
                <div className="desc">
                    <div className="con">
                        <h3>{vlerat.backdatas.project_name}</h3> <span>{vlerat.backdatas.project_tools}</span> </div>
                </div>
            </div>
        </div>

        {vlera == 1 ? <Redirect push
            to={{
                pathname: "/display",
                state: { post_id: idPost }
            }}
        /> : ''}
    </div>);
}
export default Post;