import React, { useState } from "react";
import { Link } from "react-router-dom";
//import ReactFlagsSelect from 'react-flags-select';
//import 'react-flags-select/css/react-flags-select.css';
import LocaleContext from "../globalCon";
const Topmenu = (vlerat) => {
    const [classList, setClasList] = useState(0);

    const activeClass = (vlerat) => {
        setClasList(vlerat);
        document.body.classList.remove('mobile-menu-opened');
    }

    /*  <li  >
                                <ReactFlagsSelect onSelect={vlerat.changeflag} claasName="menu-flags "
                                    countries={["US", "DE", "IT"]}
                                    customLabels={{ "US": "English", "DE": "Deutsche", "Italiano": "IT" }}
                                    placeholder="Language"
                                    showOptionLabel={true}
                                    showSelectedLabel={true}
                                    defaultCountry={locale.defaultlang} />
                            </li>*/
    return (
        <LocaleContext.Consumer>
            {(locale) => (

                <div id="site-menu">
                    <nav className="nav-menu">
                        <ul>


                            <li class="col-3 text-light">  | </li>
                            <li onClick={() => activeClass(1)}
                                className={classList == 1 ? 'current-menu-item menu-item' : 'menu-item'}>
                                <Link to="/">Home</Link></li>
                            <li className={classList == 2 ? 'current-menu-item menu-item ' : 'menu-item'}
                                onClick={() => activeClass(2)}
                            ><Link to="/resume" >Resume</Link></li>
                            <li
                                className={classList == 3 ? 'current-menu-item menu-item' : 'menu-item'}
                                onClick={() => activeClass(3)}  ><Link to="/portofolio">Portfolio</Link></li>
                            <li className={classList == 4 ? 'current-menu-item menu-item' : 'menu-item'}
                                onClick={() => activeClass(4)}  ><Link to="/contact">Contact</Link></li>
                        </ul>
                    </nav>
                </div >
            )}
        </LocaleContext.Consumer>
    );
}

export default Topmenu;