import React, { Component } from "react";
import Footer from "../footer/footer";
import axios from "axios";
import Post from "./post/post";
import LocaleContext from "../globalCon";


export default class portofolio extends Component {
    state = {
        portofolioPost: []

    }
    componentDidMount() {
        axios.get("https://dilavermusa.com/backend/showPortofolio.php").then(vlerat => {

            this.setState({ portofolioPost: vlerat.data });

        });
    }

    render() {
        return (
            <LocaleContext.Consumer>
                {(locale) => (
                    <div className="content-wrap clearfix">
                        <div className="mccan page">
                            <h3 className="title-small">Take A Look At</h3>
                            <h2 className="title">Projects</h2>
                            <div className="content">
                                <div className="grid-container">
                                    <div className="grid-column">
                                        <div className="row">
                                            {
                                                this.state.portofolioPost.map(lista => {
                                                    return <Post key={lista.id_post} backdatas={lista} />
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Footer />
                    </div>
                )}
            </LocaleContext.Consumer>
        );
    }
}