import React from "react";

export default function info() {
    const clikimi = () => {
        window.open('https://drive.google.com/file/d/1MQ2FkT59XhZfttTZQUrIM1vd-YWWGjej/view?usp=sharing', '_blank');
    }
    return (

        <div className="my-info border border-dark shadow-lg">
            <div className="box">
                <div className="field field-my-info field-separator">
                    <h6>Location</h6>
                    <p>Durres, Albania</p>
                </div>
                <div className="field field-my-info field-separator">
                    <h6>E-Mail</h6>
                    <p>rei.etrc@gmail.com</p>
                </div>
                <div className="field field-my-info field-separator">
                    <h6>Phone</h6>
                    <p>+355 69 23 59 162</p>
                </div>
                <div className="clear"></div>
                <button type="button" onClick={clikimi} >  <span className="ti-download"></span> DOWNLOAD CV</button>
            </div>
        </div >

    );


}