import React, { Component } from "react";
import "../../assetsDashboard/css/style.css";
import "../../assetsDashboard/css/components.css";
import "../../assetsDashboard/css/custom.css";
import "../../assetsDashboard/bundles/bootstrap-social/bootstrap-social.css";
import LocaleContext from "../../components/globalCon";
import { Redirect, Link } from "react-router-dom";

class Login extends Component {

    render() {
        return (
            <LocaleContext.Consumer>
                {(locale) => (locale.userid == null ?
                    < body style={{
                        backgroundImage: "url(" + "https://images2.alphacoders.com/103/thumb-1920-1039991.jpg" + ")",
                        height: '100vh'
                    }} >
                        <div id="app "  >
                            <section className="section">
                                <div className="container mt-5">
                                    <div className="row">
                                        <div className="col-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
                                            <div className="card card-primary">
                                                <div className="card-header">
                                                    <h4>Wellcome</h4>
                                                </div>
                                                <div className="card-body">
                                                    <form method="POST" action="#" className="needs-validation" novalidate="">
                                                        <div className="form-group">
                                                            <label for="email">Username:</label>
                                                            <input id="email" type="email" className="form-control bg-light" name="email" tabindex="1" required onChange={e => this.props.formdata(1, e.target.value)}></input>

                                                        </div>
                                                        <div className="form-group">
                                                            <div className="d-block">
                                                                <label for="password" className="control-label">Password:</label>

                                                            </div>
                                                            <input onChange={e => this.props.formdata(2, e.target.value)} id="password" type="password" className="form-control bg-light" name="password" tabindex="2" required />

                                                        </div>
                                                        <div className="form-group">
                                                            <button type="button" onClick={this.props.kalimi} className="btn btn-primary btn-lg btn-block" tabindex="4">
                                                                Login
                    </button>
                                                        </div>
                                                        <div className="col-12 d-flex justify-content-center">
                                                            <Link to="/" className="">Return to webstie </Link>
                                                        </div>
                                                        <div class="alert alert-danger alert-has-icon  " hidden={locale.alert}>
                                                            <div class="alert-icon"><i class="far fa-lightbulb"></i></div>
                                                            <div class="alert-body">
                                                                <div class="alert-title">Danger</div>
                        Your username or password is wrong please re-enter with you correct credencials!
                      </div>
                                                        </div>
                                                    </form>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </div>
                    </body> : <Redirect push to="/dashboard" />
                )
                }
            </LocaleContext.Consumer>
        );
    }
}

export default Login;